import React, { useState } from 'react';
import { usePharmaStore } from '../store/useStore';
import { Users, DollarSign, Calculator, Search, Plus, Trash2, Edit, FileText, Fingerprint, CheckCircle, AlertTriangle } from 'lucide-react';
import { EmployeeProfile, Role } from '../../domain/types';
import { WebAuthnService } from '../../infrastructure/biometrics/WebAuthnService';

const HRPage: React.FC = () => {
    const { employees, updateEmployeeBiometrics } = usePharmaStore();
    const [activeTab, setActiveTab] = useState<'DIRECTORY' | 'SIMULATOR'>('DIRECTORY');
    const [simulatorData, setSimulatorData] = useState({ base: 500000, gratification: 0, commission: 0 });
    const [searchTerm, setSearchTerm] = useState('');
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [editingEmployee, setEditingEmployee] = useState<EmployeeProfile | null>(null);

    // Biometric Enrollment State
    const [isEnrollmentModalOpen, setIsEnrollmentModalOpen] = useState(false);
    const [enrollmentEmployee, setEnrollmentEmployee] = useState<EmployeeProfile | null>(null);
    const [enrollmentStatus, setEnrollmentStatus] = useState<'IDLE' | 'SCANNING' | 'SUCCESS' | 'ERROR'>('IDLE');

    const handleEnrollment = async () => {
        if (!enrollmentEmployee) return;
        setEnrollmentStatus('SCANNING');

        const result = await WebAuthnService.registerCredential(enrollmentEmployee.id);

        if (result.success && result.credentialId) {
            updateEmployeeBiometrics(enrollmentEmployee.id, result.credentialId);
            setEnrollmentStatus('SUCCESS');
            setTimeout(() => {
                setIsEnrollmentModalOpen(false);
                setEnrollmentStatus('IDLE');
                setEnrollmentEmployee(null);
            }, 2000);
        } else {
            setEnrollmentStatus('ERROR');
        }
    };
    const calculateSalary = () => {
        const base = Number(simulatorData.base);
        const grat = Math.min(base * 0.25, 158333); // Tope legal aprox
        const comm = Number(simulatorData.commission);
        const taxable = base + grat + comm;
        const health = taxable * 0.07;
        const pension = taxable * 0.11; // Promedio AFP
        const liquid = taxable - health - pension;
        return { taxable, health, pension, liquid, grat };
    };

    const simulation = calculateSalary();

    return (
        <div className="p-6 bg-slate-50 min-h-screen">
            <header className="mb-8">
                <h1 className="text-3xl font-extrabold text-slate-900">Recursos Humanos</h1>
                <p className="text-slate-500">Gestión de Personal y Remuneraciones</p>
            </header>

            <div className="flex gap-4 mb-8">
                <button
                    onClick={() => setActiveTab('DIRECTORY')}
                    className={`flex items-center gap-2 px-6 py-3 rounded-xl font-bold transition ${activeTab === 'DIRECTORY' ? 'bg-blue-600 text-white shadow-lg shadow-blue-200' : 'bg-white text-slate-500 hover:bg-slate-50'}`}
                >
                    <Users size={20} /> Directorio
                </button>
                <button
                    onClick={() => setActiveTab('SIMULATOR')}
                    className={`px-6 py-3 rounded-xl font-bold flex items-center gap-2 transition ${activeTab === 'SIMULATOR' ? 'bg-slate-900 text-white shadow-lg' : 'bg-white text-slate-500 hover:bg-slate-100'}`}
                >
                    <Calculator size={20} /> Simulador Sueldo
                </button>
            </div>

            {activeTab === 'DIRECTORY' && (
                <div className="bg-white rounded-3xl shadow-sm border border-slate-200 overflow-hidden">
                    <div className="p-6 border-b border-slate-100 flex justify-between items-center">
                        <h2 className="text-xl font-bold text-slate-800">Colaboradores Activos</h2>
                        <div className="relative">
                            <Search className="absolute left-3 top-1/2 -translate-y-1/2 text-slate-400" size={18} />
                            <input type="text" placeholder="Buscar..." className="pl-10 pr-4 py-2 bg-slate-50 rounded-lg border border-slate-200 focus:outline-none focus:border-cyan-500" />
                        </div>
                    </div>
                    <table className="w-full text-left">
                        <thead className="bg-slate-50 text-slate-500 font-bold text-sm">
                            <tr>
                                <th className="p-6">Nombre</th>
                                <th className="p-6">Cargo</th>
                                <th className="p-6">Contrato</th>
                                <th className="p-6">Sueldo Base</th>
                                <th className="p-6">Acciones</th>
                            </tr>
                        </thead>
                        <tbody className="divide-y divide-gray-100">
                            {employees.map((emp) => (
                                <tr key={emp.id} className="hover:bg-slate-50 transition">
                                    <td className="p-6">
                                        <div className="flex items-center gap-3">
                                            <div className="w-10 h-10 bg-blue-100 rounded-full flex items-center justify-center text-blue-600 font-bold">
                                                {emp.name.charAt(0)}
                                            </div>
                                            <div>
                                                <div className="font-bold text-slate-800">{emp.name}</div>
                                                <div className="text-xs text-slate-400">{emp.rut}</div>
                                            </div>
                                        </div>
                                    </td>
                                    <td className="p-6">
                                        <span className="px-3 py-1 bg-blue-50 text-blue-600 rounded-full text-xs font-bold">{emp.role}</span>
                                    </td>
                                    <td className="p-6 text-slate-500">{emp.status}</td>
                                    <td className="p-6 font-mono text-slate-600">${emp.labor_data.base_salary.toLocaleString()}</td>
                                    <td className="p-6">
                                        <div className="flex gap-2 mt-4">
                                            <button
                                                onClick={() => { setEditingEmployee(emp); setIsModalOpen(true); }}
                                                className="flex-1 bg-slate-100 text-slate-600 py-2 rounded-lg text-sm font-bold hover:bg-slate-200 transition"
                                            >
                                                Editar
                                            </button>
                                            <button
                                                onClick={() => { setEnrollmentEmployee(emp); setIsEnrollmentModalOpen(true); }}
                                                className={`flex-1 py-2 rounded-lg text-sm font-bold transition flex items-center justify-center gap-2 ${emp.biometric_credentials?.length
                                                    ? 'bg-green-100 text-green-700 hover:bg-green-200'
                                                    : 'bg-blue-100 text-blue-700 hover:bg-blue-200'
                                                    }`}
                                            >
                                                <Fingerprint size={16} />
                                                {emp.biometric_credentials?.length ? 'Huella OK' : 'Enrolar'}
                                            </button>
                                        </div>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>
            )}

            {activeTab === 'SIMULATOR' && (
                <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
                    <div className="bg-white p-8 rounded-3xl shadow-sm border border-slate-200">
                        <h3 className="text-xl font-bold text-slate-800 mb-6">Parámetros</h3>
                        <div className="space-y-4">
                            <div>
                                <label className="block text-sm font-bold text-slate-700 mb-2">Sueldo Base</label>
                                <input
                                    type="number"
                                    className="w-full p-3 border-2 border-slate-200 rounded-xl focus:border-cyan-500 focus:outline-none"
                                    value={simulatorData.base}
                                    onChange={(e) => setSimulatorData({ ...simulatorData, base: Number(e.target.value) })}
                                />
                            </div>
                            <div>
                                <label className="block text-sm font-bold text-slate-700 mb-2">Comisiones Estimadas</label>
                                <input
                                    type="number"
                                    className="w-full p-3 border-2 border-slate-200 rounded-xl focus:border-cyan-500 focus:outline-none"
                                    value={simulatorData.commission}
                                    onChange={(e) => setSimulatorData({ ...simulatorData, commission: Number(e.target.value) })}
                                />
                            </div>
                        </div>
                    </div>

                    <div className="bg-slate-900 p-8 rounded-3xl text-white shadow-xl">
                        <h3 className="text-xl font-bold mb-6 flex items-center gap-2"><DollarSign /> Resultado Simulado</h3>

                        <div className="space-y-4">
                            <div className="flex justify-between text-slate-400">
                                <span>Sueldo Base</span>
                                <span>${simulatorData.base.toLocaleString()}</span>
                            </div>
                            <div className="flex justify-between text-slate-400">
                                <span>Gratificación (Tope)</span>
                                <span>${Math.round(simulation.grat).toLocaleString()}</span>
                            </div>
                            <div className="flex justify-between text-slate-400">
                                <span>Comisiones</span>
                                <span>${simulatorData.commission.toLocaleString()}</span>
                            </div>
                            <div className="h-px bg-slate-700 my-4" />
                            <div className="flex justify-between text-emerald-400 font-bold text-lg">
                                <span>Total Haberes</span>
                                <span>${Math.round(simulation.taxable).toLocaleString()}</span>
                            </div>
                            <div className="flex justify-between text-red-400 text-sm">
                                <span>Descuentos Legales (~18%)</span>
                                <span>-${Math.round(simulation.health + simulation.pension).toLocaleString()}</span>
                            </div>
                            <div className="h-px bg-slate-700 my-4" />
                            <div className="flex justify-between text-3xl font-extrabold">
                                <span>Líquido a Pagar</span>
                                <span>${Math.round(simulation.liquid).toLocaleString()}</span>
                            </div>
                        </div>
                    </div>
                </div>
            )}
            {/* Enrollment Modal */}
            {isEnrollmentModalOpen && enrollmentEmployee && (
                <div className="fixed inset-0 bg-black/60 backdrop-blur-sm z-50 flex items-center justify-center p-4">
                    <div className="bg-white rounded-3xl shadow-2xl w-full max-w-md overflow-hidden p-8 text-center">
                        <div className="w-20 h-20 bg-blue-50 rounded-full flex items-center justify-center mx-auto mb-6 text-blue-600">
                            <Fingerprint size={40} />
                        </div>
                        <h3 className="text-2xl font-bold text-slate-800 mb-2">Registro Biométrico</h3>
                        <p className="text-slate-500 mb-8">
                            Solicita a <strong>{enrollmentEmployee.name}</strong> que coloque su dedo en el lector para vincular su huella.
                        </p>

                        {enrollmentStatus === 'IDLE' && (
                            <button
                                onClick={handleEnrollment}
                                className="w-full bg-blue-600 hover:bg-blue-700 text-white py-4 rounded-xl font-bold shadow-lg shadow-blue-200 transition-all"
                            >
                                Iniciar Escaneo
                            </button>
                        )}

                        {enrollmentStatus === 'SCANNING' && (
                            <div className="flex flex-col items-center gap-4 py-4">
                                <div className="w-12 h-12 border-4 border-blue-200 border-t-blue-600 rounded-full animate-spin"></div>
                                <span className="font-bold text-blue-600">Escaneando...</span>
                            </div>
                        )}

                        {enrollmentStatus === 'SUCCESS' && (
                            <div className="flex flex-col items-center gap-4 py-4">
                                <CheckCircle size={48} className="text-green-500" />
                                <span className="font-bold text-green-600">¡Huella Registrada!</span>
                            </div>
                        )}

                        {enrollmentStatus === 'ERROR' && (
                            <div className="flex flex-col items-center gap-4 py-4">
                                <AlertTriangle size={48} className="text-red-500" />
                                <span className="font-bold text-red-600">Error al escanear. Intente nuevamente.</span>
                                <button
                                    onClick={() => setEnrollmentStatus('IDLE')}
                                    className="text-slate-500 underline mt-2"
                                >
                                    Reintentar
                                </button>
                            </div>
                        )}

                        <button
                            onClick={() => { setIsEnrollmentModalOpen(false); setEnrollmentStatus('IDLE'); }}
                            className="mt-6 text-slate-400 hover:text-slate-600 font-medium"
                        >
                            Cancelar
                        </button>
                    </div>
                </div>
            )}
        </div>
    );
};

export default HRPage;
