import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { usePharmaStore } from '../store/useStore';
import { ShoppingCart, Truck, Users, Clock, Lock, ArrowRight } from 'lucide-react';
import { motion } from 'framer-motion';

const LandingPage: React.FC = () => {
    const navigate = useNavigate();
    const { login, user } = usePharmaStore();
    const [pin, setPin] = useState('');
    const [error, setError] = useState('');
    const [isLoginModalOpen, setIsLoginModalOpen] = useState(false);
    const [targetRoute, setTargetRoute] = useState('');

    const handleCardClick = (route: string) => {
        if (user) {
            navigate(route);
        } else {
            setTargetRoute(route);
            setIsLoginModalOpen(true);
        }
    };

    const handleLogin = (e: React.FormEvent) => {
        e.preventDefault();
        if (login(pin)) {
            setIsLoginModalOpen(false);
            navigate(targetRoute || '/pos');
        } else {
            setError('PIN Incorrecto');
        }
    };

    const BentoCard = ({ title, icon: Icon, color, route, desc, customOnClick }: any) => (
        <motion.div
            whileHover={{ scale: 1.02 }}
            whileTap={{ scale: 0.98 }}
            className={`relative overflow-hidden rounded-3xl p-8 cursor-pointer shadow-xl transition-all ${color} text-white group`}
            onClick={customOnClick || (() => handleCardClick(route))}
        >
            <div className="absolute top-0 right-0 p-4 opacity-10 group-hover:opacity-20 transition-opacity">
                <Icon size={120} />
            </div>
            <div className="relative z-10 flex flex-col h-full justify-between">
                <div>
                    <div className="bg-white/20 w-12 h-12 rounded-2xl flex items-center justify-center mb-4 backdrop-blur-sm">
                        <Icon size={24} />
                    </div>
                    <h3 className="text-2xl font-bold mb-2">{title}</h3>
                    <p className="text-white/80 text-sm font-medium">{desc}</p>
                </div>
                <div className="flex items-center text-sm font-bold mt-4">
                    ACCEDER <ArrowRight size={16} className="ml-2" />
                </div>
            </div>
        </motion.div>
    );

    return (
        <div className="min-h-screen bg-slate-50 flex flex-col items-center justify-center p-6">
            <header className="mb-12 text-center">
                <h1 className="text-4xl font-extrabold text-slate-900 tracking-tight mb-2">
                    Farmacias <span className="text-cyan-600">Vallenar</span> Suit
                </h1>
                <p className="text-slate-500 font-medium">Sistema ERP Clínico Integral v2.1</p>
            </header>

            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 max-w-7xl w-full">
                {/* Kiosco (Público - No requiere login, pero en este flujo lo tratamos como módulo) */}
                <BentoCard
                    title="Kiosco de Filas"
                    icon={Users}
                    color="bg-gradient-to-br from-indigo-500 to-purple-600"
                    route="/kiosk"
                    desc="Atención al cliente y tickets"
                />

                {/* POS */}
                <BentoCard
                    title="Punto de Venta"
                    icon={ShoppingCart}
                    color="bg-gradient-to-br from-emerald-500 to-teal-600"
                    route="/pos"
                    desc="Ventas, Recetas y Caja"
                />

                {/* Logística */}
                <BentoCard
                    title="Cadena de Suministro"
                    icon={Truck}
                    color="bg-gradient-to-br from-blue-500 to-cyan-600"
                    route="/supply"
                    desc="Inventario y Proveedores"
                />

                {/* RRHH */}
                <BentoCard
                    title="Recursos Humanos"
                    icon={Clock}
                    color="bg-gradient-to-br from-orange-400 to-red-500"
                    route="/hr"
                    desc="Personal y Asistencia"
                />
            </div>

            {/* Login Modal */}
            {isLoginModalOpen && (
                <div className="fixed inset-0 bg-black/60 backdrop-blur-sm z-50 flex items-center justify-center p-4">
                    <motion.div
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        className="bg-white rounded-3xl shadow-2xl p-8 w-full max-w-md"
                    >
                        <div className="text-center mb-8">
                            <div className="bg-cyan-100 w-16 h-16 rounded-full flex items-center justify-center mx-auto mb-4">
                                <Lock className="text-cyan-700" size={32} />
                            </div>
                            <h2 className="text-2xl font-bold text-slate-900">Acceso Seguro</h2>
                            <p className="text-slate-500">Ingrese su PIN de funcionario</p>
                        </div>

                        <form onSubmit={handleLogin} className="space-y-6">
                            <div>
                                <input
                                    type="password"
                                    maxLength={4}
                                    className="w-full text-center text-4xl tracking-[1em] font-bold py-4 border-b-4 border-slate-200 focus:border-cyan-600 focus:outline-none transition-colors text-slate-800 placeholder-slate-300"
                                    placeholder="••••"
                                    value={pin}
                                    onChange={(e) => setPin(e.target.value)}
                                    autoFocus
                                />
                            </div>

                            {error && <p className="text-red-500 text-center font-medium">{error}</p>}

                            <div className="grid grid-cols-2 gap-4">
                                <button
                                    type="button"
                                    onClick={() => setIsLoginModalOpen(false)}
                                    className="py-3 rounded-xl font-bold text-slate-500 hover:bg-slate-100 transition"
                                >
                                    Cancelar
                                </button>
                                <button
                                    type="submit"
                                    className="py-3 rounded-xl font-bold bg-cyan-600 text-white hover:bg-cyan-700 shadow-lg shadow-cyan-200 transition"
                                >
                                    Ingresar
                                </button>
                            </div>
                        </form>

                        <div className="mt-8 text-center text-xs text-slate-400">
                            <p>Pines Demo: 1234 (QF), 0000 (Caja), 9999 (Admin)</p>
                        </div>
                    </motion.div>
                </div>
            )}
        </div>
    );
};

export default LandingPage;
